.main{
	background: rgb(212, 211, 201);
	width: 70%;
	margin: 0 auto;
	margin-top: 100px;
	min-height: 100px;
	border-radius: 5px;
	display: flex;
	align-items: center;
	box-sizing: content-box;
}
.message{
	font-size: 20px;
	margin-left: 40px;
	font-weight: bold;
	color: #565656;
}
.img{
	width: 90px;
	height: 90%;
	margin-left: 15px;
	border-radius: 100%;
}
.textBlock{
	width: 80%;
	height: 100%;
}
@media (max-width: 467px) {
	.img{
		height: 70px;
		width: 70px;
		margin-left: 0;
		margin-right: 10px;
	}
	.main{
		width: 90%;
	}

}
@media (max-width: 353px) {
	.message{
		font-size: 15px;
		margin-left: 20px;
	}
}

