.area{
	position: absolute;
	bottom: 20px;
	left: 0;
	right: 0;
	margin: 0 auto;
	border: none;
	outline:none;
	padding: 10px;
	width: 73%;
	height: 128px;
	border-radius: 20px;
	background: #C4C4C4;
	resize: none;
	font-size: 20px;
	color: rgba(83, 83, 83, 1);
}
.area::placeholder{
	font-size: 20px;
	color: rgba(83, 83, 83, 1);
}
.input{
	background: #5391CA;
	/*border: none;*/
	position: absolute;
	right: 16%;
	border-radius: 20px;
	width: 137px;
	height: 34px;
	color: white;
	font-size: 22px;
	bottom: 30px;
	z-index: 1;
	border: none;
	outline: none;
	transition-property: background-color;
	transition-duration: 0.3s;
}
.input:hover{
	background: rgba(59, 160, 253, 1);
}

.window{
	width: 100%;
	height: 100%;
	background :rgba(239, 250, 246, 1);
	border-left: 9px solid #C4C4C4;
	position: relative;
	z-index: 2;
}
.mainTest{
	height: 80%;
	width: 100%;
	overflow-y: auto;
}
.mainTest::-webkit-scrollbar { width: 0; }

/* ie 10+ */
.mainTest { -ms-overflow-style: none; }

/* фф (свойство больше не работает, других способов тоже нет)*/
.mainTest { overflow: -moz-scrollbars-none; }

@media (max-width: 600px) {
	.input{
		width: 30px;
		border-radius: 15px;
		font-size: 0;
		background: rgba(185, 221, 254, 1);
	}
	.area{
		height: 50px;
	}
	
}
@media (max-width: 456px) {
	.area::placeholder{
		font-size: 15px;
	}
	.area{
		font-size: 15px;
	}
}
@media (max-width: 337px) {
	.area::placeholder{
		font-size: 13px;
	}
	.area{
		font-size: 13px;
	}
}
