.messages{
	width: 100%;
	height: 850px;
	background: white;
	border: 1px solid transparent;
	box-sizing: border-box;
	border-radius: 20px;
	margin-bottom: 60px;
}
.messages h1{
	color: rgb(97, 100, 105);
	font-size: 45px;
	margin-left: 25px;
	margin-top: 40px;
}
.side{
	height: 79.2%;
	width: 100%;
	padding-right: 36px;
}
.dialogs{
	width: 30%;
	height: 100%;
}
.main{
	height: 100%;
	width: 100%;
	/*border-right: 5px solid rgb(175, 180, 189);*/
	/*border-bottom: 5px solid rgb(175, 180, 189);*/
	position: relative;

}
.flex{
	display: flex;
	width: 100%;
	height: 100%;
	flex-wrap: nowrap;
}
.area{
	position: absolute;
	bottom: 20px;
	left: 0;
	right: 0;
	margin: 0 auto;
	width: 95%;
	border: none;
	outline:none;
	padding: 10px;
}
.input{
	background: rgb(48, 252, 3);
	/*border: none;*/
	border-radius: 5px;
	width: 170px;
	height: 50px;
	color: rgb(78, 79, 78);
	font-size: 28px;
	position: absolute;
	right: 35px;
	bottom: 30px;
}
.messageImage{
	width: 200px;
	height: 200px;
	border-radius: 0;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%,-50%);
}
.selectUser{
	color: rgba(55, 55, 55, 1);
	font-size: 70px;
	position: absolute;
	left: 53%;
	top: 65%;
	transform: translate(-50%,0);
}
@media (max-width: 900px) {
	.messages{
		height: 650px;
	}
}
@media (max-width: 600px) {
	.side{
		padding-right: 0;
	}
}