.name{
	color: rgb(78, 79, 82);
	font-size: 30px;
	color: black;
	text-decoration: none;
	margin-left: 10%;
	margin-top: 18px;
}
.active .main{
	background-color: rgba(227, 226, 226, 1);
}
.active .name{
	color: rgba(100, 24, 24, 1);
}

.main{
	background: white;
	border: 5px solid #C4C4C4;
	width: 90%;
	height: 112px;
	margin-top: 70px;
	margin-left: 30px;
	display: flex;
	border-radius: 5px;
	transition-property: background-color;
	transition-duration: 0.37s;
}
.image{
	width: 27%;
	height: 80px;
	border-radius: 100%;
	margin-top: 10px;
	margin-left: 5px;
}
@media (max-width: 1445px) {
	.main{
		width: 100%;
	}
	.name{
		font-size: 23px;
	}
}
@media (max-width: 1085px) {
	.image{
		display: none;
	}
	.name{
		font-size: 30px;
		margin: auto auto;
	}
}
@media (max-width: 600px) {
	.main{
		margin-left: 0;
	}
	.name{
		font-size: 25px;
	}
}
@media (max-width: 395px) {
	.name{
		font-size: 17px;
	}
	.main{
		height: 60px;
		margin-top: 30px;
	}
}